<template>
   <div  class="upload">
    <div v-if="title" class="title">
      {{title}}
    </div>
    <div class="image-picker">
      <div v-show="selectable" class="input-item">
        <i class="iconfont iconjia"/>
        <input type="file" :multiple="multi" :accept="accept" @change="uploadEvent" :key="JSON.stringify(renderImages)"/>
      </div>
      <div v-for="(item,index) in renderImages" class="image-item" key="index">
        <div 
          class="image"
          :style='{backgroundImage: `url("${item.url}")`}'
          @click="onImageClick(index)"
        />
        <div v-if="!disable" class="image-close" @click="(e)=>remove(e,index)">
          <img src="./cha.png" alt=""/>
        </div>
        <div v-show="item.loading" class="image-bg">
          <span>{{parseInt(item.percent)}}%</span>
        </div>
        <div v-show="item.error" class="image-bg">
          <i class="iconfont iconshanchucha"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { ImagePreview, Toast } from 'vant';
  import * as qiniu from 'qiniu-js';

  Toast.allowMultiple();
  Toast.setDefaultOptions({
    duration: 2000
  })
  export default {
    props:{
      title : String, // 
      images: { //  eg: [{url:''}]
        type: Array,
        required: true
      },
      token: { // 
        type: String,
        required: true
      },
      domain: { // domain
        type: String,
        default:'http://image.haoyunqi.com.cn/'
      }, 
      onlyOne: { //falsetrue
        type: Boolean,
        default: false
      },
      fileName: { //apifileVal 
        type: String,
        default: 'fileVal'
      },
      multiple: { //  
        type: Boolean,
        default: true
      },
      accept:  { // 
        type: String,
        default: 'image/*'
      },
      fileType: String, // api
      maxLimit: { //10
        type: Number,
        default: 10
      }, 
      onceLimit: { //10
        type: Number,
        default: 10
      }, 
      disable: {  //
        type: Boolean,
        default: false
      },
    },
    data(){
      return{
        renderImages: [],
        resImages: [],
      }
    },
    computed:{
      multi(){
        return this.onlyOne ? false :  this.multiple
      },
      selectable(){
        const { onlyOne, renderImages, maxLimit, disable } = this;
        let rtn = true;
        if(onlyOne && renderImages.length === 1) rtn = false;
        if(!onlyOne && renderImages.length >= maxLimit) rtn = false;
        if(disable) rtn = false;
        return rtn
      }
    },
    watch:{
      images:{
        handler(val){
          if(val !== this.resImages){
            this.renderImages = JSON.parse(JSON.stringify(val));
            this.resImages = val;
          }
        },
        immediate: true,
        deep: true
      }
    },
    methods: {
      uploadEvent(e){
        const { onceLimit, maxLimit, renderImages } = this;
        const files = e.target.files;
        if(files.length > onceLimit){
          Toast(`${onceLimit}`);
          return
        }
        if(files.length + renderImages.length > maxLimit){
          Toast(`${maxLimit}`);
          return
        }
        for(let i=0; i<files.length; i++){
          this._upload(files[i],i+renderImages.length)
        }
      },
      _upload(file,index){
        const { token, domain, onChange, renderImages } = this;
        const _this = this;
        if(file.size > 10 * 1024 * 1024 ){
          Toast('10M');
          return
        }
        if(renderImages[index])return;
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (e) {
          _this.$set(renderImages,index,{
            url: e.target.result,
            percent: 0,
            loading: true
          })
        };
        const config = {
          useCdnDomain: true,
          region: qiniu.region.z0
        };
        let key = (new Date().getTime() + '' + ((Math.random()*1000) | 0)).substring(8);
        const arr = file.name.split('.');
        if(arr.length>1){
          key = arr[0].substr(0,4) + key + '.' + arr[arr.length-1];
        }else{
          key = arr[0].substr(0,4) + key
        }
        const observable = qiniu.upload(file, key, token, {}, config);
        observable.subscribe({
          next(res){
            const { renderImages } = _this;
            if(!renderImages[index])return;
            _this.$set(renderImages[index],'percent',res.total.percent);
          },
          error(){
            const { renderImages } = _this;
            if(!renderImages[index])return;
            _this.$set(renderImages[index],'error',true);
            _this.$set(renderImages[index],'loading',false);
          },
          complete(res){
            console.log(res.key,'key')
            const { renderImages, resImages } = _this;
            if(!renderImages[index])return;
            _this.$set(renderImages[index],'error',false);
            _this.$set(renderImages[index],'loading',false);
            _this.$set(resImages,index,{
              url: domain + res.key
            })
            _this.$emit('update:images',resImages);
          }
        })
      },
      onImageClick(index){
        ImagePreview({
          images: this.renderImages.map(item=>item.url),
          startPosition: index,
          closeOnPopstate:true
        });
      },
      remove(e,index){
        e.preventDefault();
        e.stopPropagation();
        const { renderImages, resImages } = this;
        renderImages.splice(index,1);
        resImages.splice(index,1);
        this.$emit('update:images',resImages);
      },
      parseInt(num){
        return parseInt(num)
      }
    },
  }
</script>

<style lang="less" scoped>
.upload{
  background: #fff;
  padding: 20/@rem 0 10/@rem;
  .title{
    margin-left: 30/@rem;
    white-space: nowrap;
    color: #000;
    font-size: 17px;
  }
}
.close{
  position: fixed;
  right: 20/@rem;
  top: 20/@rem;
  z-index: 100000000;
  color: #fff;
  width: 60/@rem;
  height: 60/@rem;
  text-align: center;
  line-height: 60/@rem;
  i{
    font-size: 40/@rem;
  }
}
.image-picker{
  padding: 16/@rem;
  padding-left: 30/@rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .input-item{
    width: 155/@rem;
    height: 155/@rem;
    margin-right: 10/@rem;
    border-radius: 3px;
    font-size: 0;
    border: 1px solid #ccc;
    position: relative;
    i{
      font-size: 60/@rem;
      color: #ccc;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    input{
      display: block;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
  .image-item{
    width: 155/@rem;
    height: 155/@rem;
    margin-right: 10/@rem;
    position: relative;
    .image{
      border-radius: 3px;
      width: 100%;
      height: 100%;
      background-size: cover;
    }
    .image-close{
      position: absolute;
      top:-5px ;
      left: -5px ;
      z-index:10;
      img{
        width: 32/@rem;
        height: 32/@rem;
      }
    }
    .image-bg{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0,.6);
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      border-radius: 3px;
      .iconfont{
        font-size: 40/@rem;
      }
      .icon-loading{
        color: #fff;
        animation: rotate 1s linear infinite;
      }
      .iconshanchucha{
        color: red;
      }
    }
  }
}
</style>
<style>
  .van-image-preview__overlay{
    background-color:rgba(0,0,0,.7) !important;
  }
</style>